<template>
   <div class="BXContent">
      <Loader v-if="getLoader"/>

      <div class="BXheader">
         <Header />
      </div>

      <div class="BxTitle">
         <div class="labelMovie" :class="{ BxPreload: getLoader }">
            <i 
               v-if="getLabelMovie !== 'Newest Movies' " 
               class="fa fa-arrow-alt-circle-left" 
               title="Click to go back" @click="BackTohome"></i>
               {{getLabelMovie}}
         </div>
      </div>
      
      <div class="BXmain">
         <Main 
            v-if="!getPlayrStat" 
            :getLoader="getLoader"
            :getLabelMovie="getLabelMovie"
            />
         <Player 
            v-if="getPlayrStat"
            :getLoader="getLoader"
         />
      </div>
      
   </div>
</template>

<script>
   export default {
      components:{
         Header : () => import ("../Header/Header"),
         Main : () => import ("../Main/Main"),
         Player : () => import ("../Player/Player"),
         Loader : () => import ("../Loader/Loader")
      },
      computed: {
         //Catch status Player  from state
         getPlayrStat(){
            return this.$store.state.PlayerStat
         },

         //Catch status loader from State LoaderDt
         getLoader(){
            return this.$store.state.LoaderDt
         },

          //Catch data Label from State LabelMovie
         getLabelMovie(){
            return this.$store.state.LabelMovie
         }

      },
      methods: {
         //There on Title element, showing if state getLabelMovie !== 'Newest Movies' and getPlayrStat is 'true'
         BackTohome(){
            const varPage = 1
            this.$store.dispatch('GET_FIRSTDATA', varPage);
            this.$store.commit('MutLoaderDt', true);
            this.$store.commit('MutValSearch', null)
         }
      }
      
   }
</script>

<style lang="scss">
   @import './Content.scss';
</style>


